.page {
  height: 100%;
}
.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  margin: 0;
  align-items: center;
  background-color: #ffffff;
}
.page__header {
  margin: 0;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .page__header {
    padding-top: 24px;
    padding-bottom: 24px;
  }
}
@media (min-width: 1440px) {
  .page__header {
    padding-top: 50px;
  }
}
.page__container {
  flex-grow: 1;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  position: relative;
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 100%;
    max-width: 1440px;
    padding-left: 110px;
    padding-right: 110px;
  }
}
.header {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: end;
  }
}
.header__logo {
  width: 191px;
  height: 65px;
  padding-top: 10px;
  box-sizing: border-box;
}
.header__logo:hover {
  opacity: 0.8;
}
.header__logo:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .header__logo {
    width: 174px;
  }
}
.navigation {
  position: relative;
  width: auto;
  background-color: #ffffff;
}
.navigation__toggle {
  position: absolute;
  top: -67px;
  right: -6px;
  width: 40px;
  height: 65px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation--closed .navigation__toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  right: 8px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (max-width: 767px) {
  .navigation--closed .navigation__wrapper {
    display: none;
  }
}
.navigation--opened .navigation__toggle::before,
.navigation--opened .navigation__toggle::after {
  content: "";
  position: absolute;
  top: 32px;
  right: 8px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}
.navigation__wrapper {
  position: absolute;
  top: 0;
  left: -20px;
  right: -20px;
  z-index: 51;
}
@media (min-width: 768px) {
  .navigation__wrapper {
    position: relative;
    left: 0;
    right: 0;
  }
}
.site-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    justify-content: flex-start;
    width: 440px;
  }
}
@media (min-width: 1440px) {
  .site-list {
    width: 481px;
  }
}
.site-list__item,
.site-list__item-hero {
  display: block;
  padding: 22px 20px;
  padding-bottom: 24px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
}
.site-list__item:last-child,
.site-list__item-hero:last-child {
  border-bottom: 1px solid #e6e6e6;
}
.site-list__item--current,
.site-list__item-hero--current {
  border-bottom: 2px solid #68B738;
}
@media (min-width: 768px) {
  .site-list__item--current,
  .site-list__item-hero--current {
    border-bottom: 2px solid #68B738;
  }
}
.site-list__item:hover,
.site-list__item-hero:hover {
  opacity: 0.6;
}
.site-list__item:down,
.site-list__item-hero:down {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .site-list__item,
  .site-list__item-hero {
    padding: 0 0 14px 0;
    margin-right: 35px;
    border-top: none;
  }
  .site-list__item:last-child,
  .site-list__item-hero:last-child {
    margin-right: 0;
    border-bottom: none;
  }
}
@media (min-width: 1440px) {
  .site-list__item,
  .site-list__item-hero {
    margin-right: 33px;
  }
}
@media (min-width: 1440px) {
  .site-list__item-hero--current {
    border-bottom: 2px solid #ffffff;
  }
}
.site-list__link,
.site-list__link-hero {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
}
@media (min-width: 768px) {
  .site-list__link,
  .site-list__link-hero {
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
  .site-list__link,
  .site-list__link-hero {
    font-size: 20px;
  }
}
@media (min-width: 1440px) {
  .site-list__link-hero {
    color: #ffffff;
  }
}
.main-index {
  position: relative;
  width: 100%;
  margin: 0;
}
.main-index__hero {
  position: relative;
  margin: 0;
  padding-top: 27px;
  padding-bottom: 20px;
  background: url("../img/hero/cat-background.png");
  background-size: 320px 302px;
  background-position: 50% top;
  background-repeat: no-repeat;
  box-sizing: border-box;
}
@media (min-resolution: 2dppx) {
  .main-index__hero {
    background-image: url("../img/hero/cat-background@2x.png");
    background-size: 320px auto;
  }
}
@media (min-width: 1440px) {
  .main-index__hero {
    padding-top: 87px;
    padding-bottom: 82px;
  }
}
@media (min-width: 1440px) {
  .main-index__hero::before {
    content: "";
    position: absolute;
    top: -140px;
    right: 0;
    width: 50%;
    height: 696px;
    background-image: url("../img/hero/cat-background-desktop.png");
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .main-index__hero::before {
    background-image: url("../img/hero/cat-background-desktop@2x.png");
    background-size: 720px auto;
  }
}
.main-index__hero::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 302px;
  background: rgba(104, 183, 56, 0.85);
  z-index: 1;
}
@media (min-width: 768px) {
  .main-index__hero::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-index__hero::after {
    content: "";
    display: block;
    position: absolute;
    top: -139px;
    right: 0;
    width: 50%;
    height: 694px;
    background: rgba(104, 183, 56, 0.85);
    z-index: 1;
  }
}
@media (min-width: 768px) {
  .main-index__hero {
    background: #ffffff;
  }
}
