.control {
  position: relative;
  display: block;
  padding-left: 45px;
  line-height: 30px;
}
@media (min-width: 768px) {
  .control {
    padding-left: 59px;
    line-height: 40px;
  }
}
.control__mark {
  position: absolute;
  top: -3px;
  left: 0;
  width: 30px;
  height: 30px;
  background-color: #ffffff;
  border-radius: 50%;
  border: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .control__mark {
    width: 40px;
    height: 40px;
  }
}
.control__input[type="radio"]:checked + .control__mark::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 8px;
  width: 14px;
  height: 14px;
  background-color: #68B738;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .control__input[type="radio"]:checked + .control__mark::before {
    top: 11px;
    left: 11px;
    width: 18px;
    height: 18px;
  }
}
.control__input[type="checkbox"] + .control__mark {
  border-radius: 0;
}
.control__input[type="checkbox"]:checked + .control__mark::before {
  content: "";
  position: absolute;
  top: 13px;
  left: 9px;
  width: 20px;
  height: 2px;
  background-color: #68B738;
  transform: rotate(-50deg);
}
@media (min-width: 768px) {
  .control__input[type="checkbox"]:checked + .control__mark::before {
    top: 19px;
    left: 14px;
    width: 23px;
    height: 2px;
  }
}
.control__input[type="checkbox"]:checked + .control__mark::after {
  content: "";
  position: absolute;
  top: 16px;
  left: 4px;
  width: 10px;
  height: 2px;
  background-color: #68B738;
  transform: rotate(45deg);
}
@media (min-width: 768px) {
  .control__input[type="checkbox"]:checked + .control__mark::after {
    top: 23px;
    left: 7px;
    width: 14px;
    height: 2px;
  }
}
.control__label {
  align-items: center;
}
